body {
    background: grey;
    padding-top: 5%;
    padding-left: 40%;
  }
  
  .pokeball {
    width: 200px;
    height: 200px;
    background: white;
    border-radius: 50%;
    box-shadow: 5px 5px 10px black;
  }
  
  .pokeball::before {
    content:"";
    width: 200px;
    height: 100px;
    background: red;
    border-radius: 100px 100px 0 0;
    position: absolute;
  }
  
  
  .detalles {
    width: 200px;
    height: 10px;
    background: black;
    position: absolute;
    margin-top: 95px;
  }
  
  .detalles::before {
    content:"";
    width: 50px;
    height: 50px;
    border-radius: 50%;
    border: solid 10px;
    background: white;
    position: absolute;
    margin-top: -30px;
    margin-left: 65px;
  }
  
  .detalles::after {
    content:"";
    width: 20px;
    height: 20px;
    border-radius: 50%;
    border: solid 5px grey;
    background: white;
    position: absolute;
    margin-top: -10px;
    margin-left: 85px;
  }
  
  .reflejo {
    width: 50px;
    height: 30px;
    border-radius: 50%;
    background: #ff4a4a;
    position: absolute;
    margin-top: -77px;
    margin-left: 110px;
    transform: rotate(25deg);
  }